import React from 'react';
import marked from 'marked';

const MarkdownComponent = ({ markdown }) => {
  const renderer = new marked.Renderer();

  // Customize the code block renderer
  renderer.code = (code, language) => {
    const validLanguage = window.hljs.getLanguage(language) ? language : 'plaintext';
    const highlighted = window.hljs.highlight(validLanguage, code).value;
    return `<pre><code class="hljs ${validLanguage}">${highlighted}</code></pre>`;
  };

  const htmlContent = marked(markdown, {
    renderer,
    breaks: true, // Convert line breaks to <br> tags
    gfm: true, // Enable GitHub-flavored Markdown
  });

  return (
    <div
      className="markdown-body" // Optional: Add GitHub-like styling to the entire markdown
      dangerouslySetInnerHTML={{ __html: htmlContent }}
    />
  );
};

export default MarkdownComponent;



